import ParagraphSidebar from '../components/SidebarNext';

export default function sidebar(config) {
  const { locale: paragraphLocale } = config.paragraph || {};
  const { locale: lineHeighthLocale } = config.lineheight || {};
  const { locale: indentLocale } = config.indent || {};
  return {
    sidebar: ParagraphSidebar,
    locale: {
      paragraph: {
        ...paragraphLocale,
      },
      line: {
        ...lineHeighthLocale,
      },
      indent: {
        ...indentLocale,
      },
    },
  };
}
